<template>
  <div class="content-box">
    <index-header :showinput="true" :showback="false" v-on:city="chosecity" :cityvalue="city"></index-header>
    <div class="home-content">
        <mt-swipe :auto="4000" style="height:4.7rem;">
        <mt-swipe-item >
          <img :src="imgFileUrl+'201905160126424116sv1G'" alt="">
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="bay-car">
      <router-link to="/home/Detail" tag="li">
        <div>
            <img src="../../assets/image/shop.jpg"/>
        </div>
        <span></span>
      </router-link>
      <router-link to="/story" tag="li">
        <div>
          <img src="../../assets/image/flow.jpg"/>
        </div>
        <span></span>
      </router-link>
      <li @click="service">
            <div>
              <img src="../../assets/image/serve.jpg"/>
            </div>
            <span></span>
      </li>
      <li @click="shopcar">
            <div>
              <img src="../../assets/image/financial.jpg"/>
            </div>
            <span></span>
      </li>
    </div>
    <div class="purchasing" v-if="carSeriesList.length!=0?true:false">
      <h4></h4>
      <div class="purchasing-list">
        <li v-for="(item,index) in carSeriesList">
          <img :src="imgFileUrl+item.classPic" style="height:172px;" />
          <!--<div class="color" :style="carSeriesList[index].seriesFinaceList.length==0?'bottom:4.63rem':'bottom:5.3rem'" >-->
            <!--<div class="color-center">-->
              <!--<span v-for="item in carSeriesList[index].colorList" :style="{backgroundColor:item.colourValue}"></span>-->
            <!--</div>-->
          <!--</div>-->
          <div class="bg">
            <div class="type"><span>{{item.classNm}}&nbsp;&nbsp;</span><span class="font-n">&nbsp;{{item.carProductPriceRange}}</span></div>
            <!--<div class="check"></div>-->
            <div class="check">
              <div>{{item.parentClassNm}}<span v-if="!!item.compartmentType">/</span>{{item.compartmentType}}<span v-if="!!item.carProductKind">/</span>{{item.carProductKind}} <span style="margin-left: 0.3rem;color: #FC8328;">{{item.carProductSalePriceRange}}</span></div></div>

            <div class="way"><span v-for="itemD in carSeriesList[index].seriesFinaceList">{{itemD}}</span></div>
          </div>
          <!--<router-link :to="{ name:'carplan', params: {seriesId: item.id,seriesName: item.classNm} }">-->
            <!--paramsname-->
            <mt-button @click="toCarType(item.id,item.classNm)" type="primary" size="large"></mt-button>
          <!--</router-link>-->
        </li>
      </div>
    </div>
        <v-distpicker type="mobile" @selected='selected' v-show="addInp" class="area1">
        </v-distpicker>
        <div class="mask" v-show="mask"></div>
    <!--<common-plan></common-plan>-->
    <common-process></common-process>
    <common-new></common-new>
    <!--<div class="footer"></div>-->
  </div>
</template>

<script>
import {mapMutations, mapGetters, mapState} from 'vuex'
import commonHeader from 'common/common-header'
import commonPlan from '../purchasingPlan/purchasingplan'
import commonProcess from '../wholeProcess/wholeProcess'
import commonNew from '../new/new'
import indexHeader from 'common/index-header'
import * as homeApi from '../../api/home-api'
import car1 from '../../assets/image/shopcar1.png'
import car2 from '../../assets/image/shopcar2.png'
import { MessageBox } from 'mint-ui'
import { imgFileUrl } from '../../apiconfig/index'
import { Toast } from 'vant'

import VDistpicker from 'v-distpicker'
export default {
  data () {
    return {
      city:'',
      addInp :false,
      mask:false,
      imgFileUrl: imgFileUrl,
      num: 0,
      carSeriesList: [],
      imgUrl: car1,
      page: {
        pageNumber: 1,
        pageSize: 4
      }
    }
  },
  created() {
    this.getCarSeriesList()
  },
  methods: {
    selected(data){
      this.mask =false;
      this.addInp = false;
      this.city =data.province.value
      console.log(this.city,'DATA')
    },
    chosecity(){
      this.mask = true;
      this.addInp = true;

    },
    ...mapMutations({
      setNum: 'SET_NUM'
    }),
    getCarSeriesList() {
      let data = {
        pageNumber: this.page.pageNumber,
        pageSize: this.page.pageSize
      }
      homeApi.selectCarSeriesPage(data).then(res => {
        if (res.data.code == 200) {
          this.carSeriesList = res.data.page.list
        } else {
          Toast(res.data.msg)
        }
      }).catch(err => {
        console.log(err)
      })
    },
    toDetail() {
      this.$router.togo('/Home/Detail')
    },
    service() {
      this.$router.push('/service')
    },
    shopcar() {
      this.$router.push('/wholeprocess')
    },
    toNews() {
      MessageBox.alert('...', '')
    },
    toCarType(id,name){
      localStorage.setItem('seriesId',id);
      localStorage.setItem('seriesName',name);
      this.$router.push({path:'/info',name:'carplan'})
    }
  },
  components: {
    commonHeader,
    commonPlan,
    commonProcess,
    commonNew,
    indexHeader,
    VDistpicker
  },
  computed: {
    ...mapGetters([
      'number'
    ]),
    ...mapState({
      number: state => state.home.number
    })
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import "~styles/index.less";
@import "~styles/variable.less";
li{
  list-style: none;
}
.area1{
  width: 100%;
  height: 90%;
  position:fixed;
  left: 0;
  bottom: 0;
  overflow-y: scroll;
}
.distpicker-address-wrapper .address-header ul {
  position: fixed !important;
  left: 0 !important;
  top: 0 !important;

}
.page-content{
}
.content-box{
  .fs(30);
}
.home-content{
  .mint-swipe-items-wrap{
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
}
.bay-car{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#fff;
  height: 2.4rem;
  margin-bottom: 0.2rem;
  .pl(10);
  .pr(10);
  li{
    width: 25%;
    height: 2rem !important;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    span{
      .pt(10);
    }
    div{
      display: flex;
      justify-content: center;
      width: 1rem;
      height: 1rem;
      margin-bottom: 0.1rem;
      .fs(26);
      >img{
        width: 120%;
        height: 120%;
      }
    }
  }
}
.header-box{
  position: absolute;
  width: 100%;
  .h(100);
  .lh(100);
  background-color: @base-color;
  color: @base-header-color;
  .fs(@base-header-size);
  display: flex;
  z-index: 1;
  .left-icon{
    position: relative;
    flex: 1;
    img{
      width: 20%;
      height: 30%;
      vertical-align: middle;
    }
    .locationFont{
      .fs(28);
    }
    .icon-back{
      position: absolute;
      display: inline-block;
      .w(50);
      .h(50);
      .left(25);
      .top(25);
      background-image: url("../../assets/imgs/w-back.svg");
      background-size: cover;
    }
  }
  .header-tittle{
    flex: 3;
  }
  .right-icon{
    position: relative;
    flex: 1;
    img{
      width: 30%;
      vertical-align: middle;
      .mr(-50);
    }
    .icon{
      position: absolute;
      display: inline-block;
      .w(50);
      .h(50);
      .right(25);
      .top(25);
      background-image: url("../../assets/imgs/more.svg");
      background-size: cover;
    }
  }
}
.purchasing {
  height: auto;
  background: #fff;
  .fs(30);
  > h4 {
    color: #FF6300;
    font-weight: 700;
    text-align: left;
    .pl(30);
    .pr(30);
    .pt(20);
    .pb(20);
    .fs(35);
  }
  .purchasing-list {
    width: 100%;
    li {
      width: 100%;
      height: auto;
      position: relative;
      .pl(30);
      .pr(30);
      .pb(45);
      .mint-button{
        height: 1.2rem;
        margin-bottom: 0.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #F47442;
        .fs(30);
        border-radius:0px 0px 4px 4px;
      }
      .check{
        width: 100%;
        display: flex;
        justify-content:flex-start;
        padding-left: 0.5rem;
        .pb(10);

        flex-wrap: wrap;
        div:nth-child(1){
          .mr(90)
        }
      }
      .way{
        width: 100%;
        display: flex;
        justify-content: flex-start;
        padding-left: 0.5rem;
        .mt(10);
        padding-bottom: 0.42rem;
        .fs(26);
        span{
          display: inline-block;
          padding:0.08rem;
          border-radius:2px;
          border: 1px solid #FC904C;
          color: #FC904C;
          display: flex;
          align-items: center;
          justify-content: center;
          .mr(10);
        }
        span:last-child{
          .mr(0);
        }
      }
      img {
        width: 100%;
        height: 100%;
        border-radius: 4px 4px 0px 0px;
        dispaly:block;
        vertical-align:top;
      }
      .bg{
        background-color:#f5f5f5;
      }
      .color{
        position: absolute;
        bottom: 5.3rem;
        left:50%;
        /*width:100%;*/
        .color-center{
          position: relative;
          left: -50%;
          span {
            width: 0.6rem;
            height: 0.6rem;
            display: inline-block;
            border-radius: 2px;
            .mr(10);
          }
          span:last-child {
            .mr(0);
          }
        }
      }
      .type{
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        padding-bottom: 0.16rem;
        .pt(30);
        span{
          .fs(35);
          font-weight: 600;
        }
        span:nth-child(2){
          color: #FE5330;
          font-weight: 500;
          font-size: 0.4rem;
        }

      }
    }
    li:last-child{
      .pb(45);
    }
  }
}
</style>



